.TimeTable {
	background-color: #f0f0f0;

	table {
		width: 50%;
		margin: 0 auto;

		tr {
			th {
				width: 33.333%;
				padding: 10px 0;
				text-align: left;
				position: relative;

				&:nth-of-type(1) {
					&::before {
						content: '';
						position: absolute;
						top: 0;
						left: 0;
						display: block;
						width: 70%;
						height: 350px;
						background-color: $pink;
						opacity: 0.3;
						border-radius: 100px;
					}
				}

				&:nth-of-type(2) {
					&::before {
						content: '';
						position: absolute;
						top: 350px;
						left: 0;
						display: block;
						width: 130px;
						height: 130px;
						background-color: $green;
						opacity: 0.5;
						border-radius: 100px;
					}
				}

				&:nth-of-type(3) {
					&::before {
						content: '';
						position: absolute;
						top: 180px;
						left: 0;
						display: block;
						width: 150px;
						height: 500px;
						background-color: $sky;
						opacity: 0.5;
						border-radius: 100px;
					}
				}

				strong {
					font-size: $fontXXL;
				}
			}

			td {
				padding: 40px 0;
				span {
					color: $black;
					font-size: $fontS;
				}

				p {
					color: $black;
					font-weight: bold;
					font-size: $fontM;
					padding: 0 10px;
				}
			}
		}
	}
}

@media screen and(max-width: $tablet) {
	.TimeTable {
		background-color: #f0f0f0;

		table {
			width: 80%;
			margin: 0 auto;

			tr {
				th {
					width: calc(80% / 3);
					padding: 0;

					&:nth-of-type(1) {
						&::before {
							display: none;
						}
					}

					&:nth-of-type(2) {
						&::before {
							display: none;
						}
					}

					&:nth-of-type(3) {
						&::before {
							display: none;
						}
					}

					strong {
						font-size: $fontXXL;
					}
				}

				td {
					padding: 50px 0;
				}
			}
		}
	}
}
