.pop {
	width: 100%;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.8);
	position: fixed;
	top: 0;
	left: 0;
	z-index: 3;
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: 'Montserrat', sans-serif;

	article {
		width: 100vw;
		height: 90vh;
		position: fixed;
		bottom: 0;
		left: 0;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			filter: grayscale(0.8);
			border-top-left-radius: 50px;
			border-top-right-radius: 50px;
		}
		.text {
			position: absolute;
			top: 30%;
			left: 30px;
			width: 50%;
			color: #fff;

			h3 {
				color: $white;
				font-size: $fontXXXL;
				margin-bottom: 30px;
			}

			span {
				display: block;
				color: #fff;
				margin-bottom: 30px;
				font-size: $fontXL;
			}

			p {
				color: #dcdcdc;
				line-height: 1.55;
				margin-bottom: 20px;
			}

			.artist-sns {
				display: flex;

				li {
					margin-right: 20px;
				}

				li a svg {
					color: #fff;
					width: 25px;
					height: 25px;
				}
			}
		}

		.popCloseBtn {
			position: absolute;
			top: 0;
			right: 20px;
			cursor: pointer;
			z-index: 3;
			width: 70px;
			height: 70px;
			z-index: 4;

			span {
				display: block;
				width: 100%;
				height: 100%;
				font-weight: 700;
				font-size: $fontL;
				color: #fff;
				text-transform: uppercase;
				line-height: 70px;
				z-index: 4;
				text-align: center;
			}
		}
	}
}
